interface Project {
  name: string;
  description: string;
  githubUrl: string;
}

class PortfolioApp {
  private isDarkMode: boolean = false;
  private isMobileMenuOpen: boolean = false;
  private projects: Project[] = [
    {
      name: "Dungeon Dive",
      description:
        "This is a simple dungeon crawler game made with C programming language where the player can navigate through a randomly generated dungeon after entering their username and dungeon size.",
      githubUrl: "https://github.com/0xRum/dungeon-dive",
    },
    {
      name: "Taskii",
      description:
        "A modern and efficient task management application built with Flutter. Taskii helps you organize, prioritize, and track your tasks seamlessly, whether you're managing personal to-dos or team projects.",
      githubUrl:
        "https://github.com/vasu-patel-123/Team-HamburgerHelpers-final-Project/tree/main/taskii",
    },
  ];

  constructor() {
    this.initializeThemeToggle();
    this.initializeSmoothScroll();
    if (
      window.location.pathname.endsWith("index.html") ||
      window.location.pathname.endsWith("/")
    ) {
      this.loadProjects();
    }
    this.initializeIntersectionObserver();
    this.initializeMobileMenu();
    this.initializeSkillsCarousel();
  }

  private initializeThemeToggle(): void {
    const prefersDark = window.matchMedia(
      "(prefers-color-scheme: dark)"
    ).matches;
    const storedTheme = localStorage.getItem("theme");

    if (storedTheme) {
      document.documentElement.setAttribute("data-theme", storedTheme);
    } else if (prefersDark) {
      document.documentElement.setAttribute("data-theme", "dark");
      localStorage.setItem("theme", "dark");
    }

    const toggleTheme = () => {
      const currentTheme = document.documentElement.getAttribute("data-theme");
      const newTheme = currentTheme === "dark" ? "light" : "dark";

      document.documentElement.setAttribute("data-theme", newTheme);
      localStorage.setItem("theme", newTheme);
    };

    // Add event listeners for theme toggle buttons
    document
      .querySelector(".theme-toggle")
      ?.addEventListener("click", toggleTheme);
    document
      .querySelector(".theme-toggle-mobile")
      ?.addEventListener("click", toggleTheme);
  }

  private initializeSmoothScroll(): void {
    document.querySelectorAll('a[href^="#"]').forEach((anchor) => {
      anchor.addEventListener("click", (e) => {
        e.preventDefault();
        const target = document.querySelector(
          (e.target as HTMLAnchorElement).getAttribute("href") || ""
        );
        target?.scrollIntoView({
          behavior: "smooth",
        });
      });
    });
  }

  private loadProjects(): void {
    const projectsGrid = document.querySelector(".projects-grid");
    if (!projectsGrid) return;

    this.projects.forEach((project) => {
      const projectCard = this.createProjectCard(project);
      projectsGrid.appendChild(projectCard);
    });
  }

  private createProjectCard(project: Project): HTMLElement {
    const card = document.createElement("div");
    card.className = "project-card";

    card.innerHTML = `
      <div class="card-header">
        <h3>${project.name}</h3>
      </div>
      <p>${project.description}</p>
      <a href="${project.githubUrl}" target="_blank" rel="noopener noreferrer">
        View on GitHub
        <i class="fab fa-github"></i>
      </a>
    `;

    return card;
  }

  private initializeIntersectionObserver(): void {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.classList.add("visible");
          }
        });
      },
      {
        threshold: 0.1,
      }
    );

    document.querySelectorAll("section").forEach((section) => {
      observer.observe(section);
    });
  }

  private initializeMobileMenu(): void {
    const mobileMenu = document.querySelector(".mobile-menu");
    const menuToggle = document.querySelector(
      ".menu-toggle"
    ) as HTMLButtonElement;
    const closeMenu = document.querySelector(".close-menu");
    const mobileMenuLinks = document.querySelectorAll(".mobile-nav a");

    if (menuToggle && closeMenu && mobileMenu) {
      menuToggle?.addEventListener("click", () => {
        this.isMobileMenuOpen = true;
        mobileMenu.classList.add("active");
        menuToggle.style.display = "none";
        document.body.style.overflow = "hidden";
      });

      closeMenu?.addEventListener("click", () => {
        this.isMobileMenuOpen = false;
        mobileMenu.classList.remove("active");
        menuToggle.style.display = "block";
        document.body.style.overflow = "";
      });

      mobileMenuLinks.forEach((link) => {
        link.addEventListener("click", () => {
          this.isMobileMenuOpen = false;
          mobileMenu.classList.remove("active");
          menuToggle.style.display = "block";
          document.body.style.overflow = "";
        });
      });
    }
  }

  private initializeSkillsCarousel(): void {
    const carousel = document.querySelector(".skills-carousel");
    if (!carousel) return;

    const skillsScroll = carousel.querySelector(
      ".skills-scroll"
    ) as HTMLElement;
    const prevButton = carousel.querySelector(
      ".carousel-control.prev"
    ) as HTMLElement;
    const nextButton = carousel.querySelector(
      ".carousel-control.next"
    ) as HTMLElement;
    const skillItems = carousel.querySelectorAll(".skill-item");

    if (!skillsScroll || !prevButton || !nextButton || !skillItems.length)
      return;

    let currentIndex = 0;
    let autoScrollInterval: number | null = null;
    const itemsPerView =
      window.innerWidth < 640 ? 1 : window.innerWidth < 1024 ? 2 : 3;
    const totalItems = skillItems.length;
    const maxIndex = totalItems - itemsPerView;

    const updateCarousel = () => {
      const offset = currentIndex * (100 / itemsPerView);
      skillsScroll.style.transform = `translateX(-${offset}%)`;
    };

    const handlePrev = () => {
      currentIndex = Math.max(0, currentIndex - 1);
      updateCarousel();
      resetAutoScroll();
    };

    const handleNext = () => {
      currentIndex = Math.min(maxIndex, currentIndex + 1);
      updateCarousel();
      resetAutoScroll();
    };

    const startAutoScroll = () => {
      if (autoScrollInterval) return;
      autoScrollInterval = window.setInterval(() => {
        if (currentIndex >= maxIndex) {
          currentIndex = 0;
        } else {
          currentIndex++;
        }
        updateCarousel();
      }, 2000); // Scroll every 3 seconds
    };

    const stopAutoScroll = () => {
      if (autoScrollInterval) {
        clearInterval(autoScrollInterval);
        autoScrollInterval = null;
      }
    };

    const resetAutoScroll = () => {
      stopAutoScroll();
      startAutoScroll();
    };

    // Add hover pause functionality
    carousel.addEventListener("mouseenter", stopAutoScroll);
    carousel.addEventListener("mouseleave", startAutoScroll);

    // Add touch events for mobile
    let touchStartX = 0;
    let touchEndX = 0;

    carousel.addEventListener("touchstart", ((e: TouchEvent) => {
      touchStartX = e.changedTouches[0].screenX;
      stopAutoScroll();
    }) as EventListener);

    carousel.addEventListener("touchend", ((e: TouchEvent) => {
      touchEndX = e.changedTouches[0].screenX;
      const diff = touchStartX - touchEndX;

      if (Math.abs(diff) > 50) {
        // Minimum swipe distance
        if (diff > 0) {
          handleNext();
        } else {
          handlePrev();
        }
      }
      startAutoScroll();
    }) as EventListener);

    prevButton.addEventListener("click", handlePrev);
    nextButton.addEventListener("click", handleNext);

    // Handle window resize
    window.addEventListener("resize", () => {
      const newItemsPerView =
        window.innerWidth < 640 ? 1 : window.innerWidth < 1024 ? 2 : 3;
      if (newItemsPerView !== itemsPerView) {
        currentIndex = 0;
        updateCarousel();
        resetAutoScroll();
      }
    });

    // Start auto-scrolling
    startAutoScroll();
  }
}

// Initialize the app when the DOM is loaded
document.addEventListener("DOMContentLoaded", () => {
  new PortfolioApp();
});
